body {
    background-color: #000000;
}

.app-container {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    color: white;
    font-family: 'NanumSquare';
}
.unit {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}
.video-container {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: auto;
    height: auto;
    min-width: 100%;
    min-height: 100%;
}
.txt_pack {
    position: absolute;
    bottom: 80px;
    left: 100px;
    z-index: 1;
}
.txt1 {
    font-size: 56px;
    font-family: "NanumSquare";
    color: #F58220;
    line-height: 1.3;
}
.txt2 {
    margin-top: 24px;
    font-size: 16px;
    font-family: "NanumSquare";
    color: #F58220;
    line-height: 2;
}

.value-container {
    margin-top: 100vh;
    width: 1000px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}
.value-container img {
    width: 100%;
    height: auto;
    opacity: 0;
    animation: slide-up 1s ease forwards;
}

.kimhongki{
    margin-top:50px;
    width: 1000px;
    background-color: #1f1f1f;
    opacity: 0;
    animation: slide-up 1s ease forwards;
}
.kimhongki:hover {
    border-radius: 50px;
    background-color: rgb(99, 90, 113);
}
.leewonsuk{
    margin-top:50px;
    width: 1000px;
    background-color: #1f1f1f;
    opacity: 0;
    animation: slide-up 1s ease forwards;
}
.leewonsuk:hover {
    border-radius: 50px;
    background-color: rgb(99, 90, 113);
}
@keyframes slide-up {
    from {
    opacity: 0;
    transform: translateY(100%);
    }
    to {
    opacity: 1;
    transform: translateY(0);
    }
}
  
.value-container .value1 { animation-delay: 0.1s; }
.value-container .value2 { animation-delay: 0.2s; }
.value-container .value3 { animation-delay: 0.3s; }
.value-container .value4 { animation-delay: 0.4s; }
.value-container .value5 { animation-delay: 0.5s; }
.leewonsuk { animation-delay: 0.7s; }
.kimhongki { animation-delay: 0.9s; }