.header-container {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px 5%;
  font-family: 'NanumSquare';
}
.inner-container{
  display: flex;
  justify-content: space-between;
}
.letter-frame{
  color: white;
  width: 40%;
  min-width: 300px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-weight: 700;
}
.menu-frame{
  color: white;
  width: 13%;
  min-width: 130px;
  max-width: 250px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-weight: 700;
}
.login-button, .search-button, .menu-button {
  color: white;
  cursor: pointer;
}


@import url('https://cdn.rawgit.com/moonspam/NanumSquare/master/nanumsquare.css');